import React, { useReducer, useContext, useEffect, useState } from "react";
import { Store } from "../Store/Store";
import Loadingbox from "./../LoadingBox/Loadingbox";
import MessageBox from "./../MessageBox/MessageBox";
import { useNavigate, useParams, Link } from "react-router-dom";
import axios from "axios";
import { getError } from "./../Error/Error";
import { Helmet } from "react-helmet-async";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import { useSnackbar } from "notistack";
import ListGroup from "react-bootstrap/ListGroup";
import Card from "react-bootstrap/Card";
import PaystackPop from '@paystack/inline-js'
import "../Order/order.css"



function reducer(state, action) {
  switch (action.type) {
    case "FETCH_REQUEST":
      return { ...state, loading: true, error: "" };
    case "FETCH_SUCCESS":
      return { ...state, loading: false, order: action.payload, error: "" };
    case "FETCH_FAIL":
      return { ...state, loading: false, error: action.payload };
    case "PAY_REQUEST":
      return { ...state, loadingPay: true };
    case "PAY_SUCCESS":
      return { ...state, loadingPay: false, successPay: true };
    case "PAY_FAIL":
      return { ...state, loadingPay: false, errorPay: action.payload };
    case "PAY_RESET":
      return { ...state, loadingPay: false, successPay: false };

    default:
      return state;
  }
}
function Order() {
  const navigate = useNavigate();
  const params = useParams();
  const { enqueueSnackbar } = useSnackbar();
  const { id: orderId } = params;
  const { state } = useContext(Store);
  const { userInfo } = state;
  const [
    { loading, error, order, successPay },
    dispatch,
  ] = useReducer(reducer, {
    loading: true,
    order: {},
    error: "",
    successPay:false,
  
    loadingPay: false,
  });

  
 const [userToken, setuserToken] = useState("")
  useEffect(() => {
    const fetchOrder = async () => {
      try {
        dispatch({ type: "FETCH_REQUEST" });
        const { data } = await axios.get(`/api/orders/${orderId}`, {
          headers: { authorization: `Bearer ${userInfo.token}` },
        });
        dispatch({ type: "FETCH_SUCCESS", payload: data });
      } catch (err) {
        dispatch({ type: "FETCH_FAIL", payload: getError(err) });
      }
    };
    if (!userInfo) {
      return navigate("/login");
    }
    if (!order._id ||successPay || (order._id && order._id !== orderId)) {
      fetchOrder();
      if(successPay){
        dispatch({type:"PAY_RESET"})
      }
     
    } else {
      const loadPayStack = async () => {
        const { data: clientId } = await axios.get("/api/keys/paystack", {
          headers: { authorization: `Bearer ${userInfo.token}` },
        });   
        setuserToken(clientId)

      };
      loadPayStack();
    }
  }, [order, userInfo, orderId, navigate, successPay]);

  function payWithPaystack( actions) {

  
      
    
       const paystack= new PaystackPop()
      paystack.newTransaction({
        key:userToken,
        amount:order.totalPrice *100,
        email:userInfo.email,
        first_name:userInfo.name,
        onSuccess (data,transaction){
          try {
            dispatch({type:"PAY_REQUEST"})
            const {data}=  axios.put(
              `/api/orders/${order._id}/pay`,
              transaction,{
                headers:{authorization:`Bearer ${userInfo.token}`}
              }
            )
            dispatch({type:"PAY_SUCCESS", payload:data})
            enqueueSnackbar(`Successful`, { variant: 'success' });
            
          } catch (err) {
            dispatch({type:"PAY_FAIL", payload:getError(err)})
            enqueueSnackbar(getError(err), { variant: 'error' });
            
          }
        }
        

        
      })
  }
  return loading ? (
    <Loadingbox></Loadingbox>
  ) : error ? (
    <MessageBox></MessageBox>
  ) : (
    <div>
      <Helmet>
        <title>Order {orderId}</title>
      </Helmet>

      <h1>Order {orderId}</h1>
      <Row>
        <Col md={8}>
          <Card className="mb-3">
            <Card.Body>
              <Card.Title>Payment </Card.Title>
              <Card.Text>
                <strong>Method:</strong> {order.paymentMethod}
              </Card.Text>
              {order.isPaid ? (
                <MessageBox variant="success">
                  Paid at {order.paidAt}
                </MessageBox>
              ) : (
                <MessageBox variant="danger">Not Paid</MessageBox>
              )}
            </Card.Body>
          </Card>
          <Card className="mb-3">
            <Card.Body>
              <Card.Title>Items</Card.Title>
              <ListGroup variant="flush">
                {order.orderItems.map((item) => (
                  <ListGroup.Item key={item.id}>
                    <Row className="align-items-center">
                      <Col md={6}>
                        <img
                          src={item.image}
                          alt={item.nameP}
                          className="img-fluid rounded img-thumbnail"
                        />
                        <Link to={`/product/${item.caption}`}>{item.name}</Link>
                      </Col>
                      <Col md={3}>
                        <span>{item.quantity}</span>
                      </Col>
                      <Col md={3}>
                        <span>{item.price}</span>
                      </Col>
                    </Row>
                  </ListGroup.Item>
                ))}
              </ListGroup>
            </Card.Body>
          </Card>
        </Col>
        <Col md={4}>
          <Card className="mb-3">
            <Card.Body>
              <Card.Title>Order Summary</Card.Title>
              <ListGroup variant="flush">
                <ListGroup.Item>
                  <Row>
                    <Col>Items</Col>
                    <Col>#{order.itemsPrice.toFixed(2)}</Col>
                  </Row>
                </ListGroup.Item>
                <ListGroup.Item>
                  <Row>
                    <Col>Shipping</Col>
                    <Col>#{order.shippingPrice.toFixed(2)}</Col>
                  </Row>
                </ListGroup.Item>
                <ListGroup.Item>
                  <Row>
                    <Col>
                      <strong>Total:</strong>
                    </Col>
                    <Col>
                      <strong>#{order.totalPrice.toFixed(2)}</strong>{" "}
                    </Col>
                  </Row>
                </ListGroup.Item>
                {
                  order.isPaid===false?  <button onClick={()=>payWithPaystack()} className="payNowBtn"> Pay Now</button>:""
                }
               
                  
                  </ListGroup>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </div>
  );
}

export default Order;
